html {
  box-sizing: border-box;
  overflow-x: hidden;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
}
h1,
h2,
ul,
li,
figure {
  list-style: none;
  margin: 0;
}
a {
  text-decoration: none;
}
.header-top,
.header-main,
.support,
.categories,
.footer {
  max-width: 1440px;
  margin: 0 auto;
}
/* container */
.header-top,
.header-main,
.slider,
.footer {
  position: relative;
}
.header-top::after,
.header-main::after,
.slider::after,
.footer::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}
.icon {
  height: 10px;
  width: 10px;
  margin-right: 20px;
}
.social {
  height: 34px;
  width: 34px;
  fill: #d9bf8f;
  margin-right: 12px;
}
.social:hover {
  fill: #000;
}
.sup_icon {
  fill: #fff;
}
.s_air {
  height: 38px;
  width: 38px;
  margin-right: 33px;
}
.s_time {
  height: 42px;
  width: 41px;
  margin-right: 22px;
}
.s_help {
  height: 48px;
  width: 48px;
  margin-right: 28px;
}
.footer-icon {
  fill: #000;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}
