.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f2f2f2;
  padding: 14px 10px;
}
.header-top__phone {
  display: flex;
  align-items: center;
}
.header-top__phone-link {
  font-size: 10.6px;
  color: #000;
}
.header-top__phone-link:hover {
  color: #d9bf8f;
  fill: #d9bf8f;
}
.header-top__bar {
  font-size: 11px;
}
.bar-list {
  display: flex;
}
.bar-item:not(:last-child) {
  border-right: 1px solid #000;
}
.bar-item:first-child {
  padding-right: 22px;
}
.bar-item:nth-child(2) {
  padding-right: 35px;
}
.bar-item:nth-child(3) {
  padding-right: 33px;
}
.bar-item:first-child,
.bar-item:nth-child(2),
.bar-item:nth-child(3) {
  margin-right: 33px;
}
.bar-item__link {
  color: #000;
}
.bar-item__link:hover {
  color: #d9bf8f;
}
/*  header-main  */
.header-main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
.menu-list {
  display: flex;
  padding: 0;
}
.menu-item:first-child {
  margin-right: 27px;
}
.menu-item:nth-child(2) {
  margin-right: 15px;
}
.menu-item:nth-child(3) {
  margin-right: 26px;
}
.menu-item__link {
  font-size: 18px;
  font-style: italic;
  color: #000;
}
.menu-item__link:hover {
  color: #d9bf8f;
}
.header-main__logo {
  padding: 32px 0;
  padding-left: 50px;
}
.logo-title {
  font-size: 72px;
  line-height: 0.8;
}
.header-main__social-media {
  display: flex;
  align-items: center;
}
.social-media__form {
  color: #d9bf8f;
  margin-left: 32px;
}
.form-currency {
  padding: 14px;
  background: #000;
  font-size: 12px;
  color: #fff;
  border: 0;
}
.form-currency:hover {
  color: #d9bf8f;
  cursor: pointer;
}
.option {
  padding: 2px;
  color: inherit;
}
.slider-main {
  position: relative;
  text-align: center;
  text-transform: uppercase;
  line-height: 0.8;
}
.slider-back {
  width: 100%;
  height: auto;
}
.slider-item__content {
  position: absolute;
  top: 14.9%;
  left: calc(50% - 260px/2);
  z-index: 55;
}
.slider-item {
  border: 2px solid #fff;
  padding: 6px;
}
.content-info {
  display: flex;
  flex-direction: column;
  min-width: 257px;
  border: 1px solid #fff;
  padding: 29px 0 33px 0;
  font-weight: bold;
}
.content-info__title {
  font-size: 19.44px;
  color: #fff;
}
.content-info__offer {
  padding-top: 38px;
  padding-bottom: 34px;
  font-size: 58.81px;
  color: #000;
}
.content-info__action {
  position: relative;
  font-size: 19.6px;
  color: #fff;
}
.content-info__action-decor {
  position: relative;
}
.content-info__action-decor::before,
.content-info__action-decor::after,
.content-info__action::before,
.content-info__action::after {
  content: '';
  position: absolute;
  height: 1px;
  background-color: #fff;
}
.content-info__action-decor::before,
.content-info__action::before {
  width: 33px;
}
.content-info__action-decor::before {
  top: 31%;
  left: 21.5%;
}
.content-info__action::before {
  top: 35%;
  right: -74%;
}
.content-info__action-decor::after,
.content-info__action::after {
  width: 60px;
}
.content-info__action-decor::after {
  left: 11%;
  bottom: 7%;
}
.content-info__action::after {
  left: 118%;
  bottom: 20%;
}
.content-info__sale {
  padding-top: 28px;
  font-size: 31.36px;
  color: #000;
}
.slider-btn {
  position: absolute;
  left: calc(50% - 189px/2);
  margin-top: 20%;
  padding: 19px 34px 16px 25px;
  font-size: 24px;
  font-weight: bold;
  color: #fff;
  background: #f09d66;
}
.support {
  display: flex;
  justify-content: space-between;
  padding: 66px 0;
}
.support-item {
  display: flex;
  align-items: center;
  flex-basis: 30%;
  padding: 28px 0 23px 30px;
  color: #fff;
  background: #d9bf8f;
}
.support-item:nth-child(2) {
  background: #000;
  flex-basis: 40%;
}
.support-item__content {
  display: flex;
  flex-direction: column;
}
.support-item__title {
  text-transform: uppercase;
  line-height: 0.7;
  font-size: 30px;
  font-weight: bold;
  padding-bottom: 17px;
}
.support-item__text {
  text-transform: capitalize;
  font-size: 14px;
}
.categories-header {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: 34px;
}
.categories-header::before,
.categories-header::after {
  content: '';
  position: absolute;
  height: 1px;
  width: 30%;
  background: #e2e2e2;
}
.categories-header::before {
  left: 0;
}
.categories-header::after {
  right: 0;
}
.categories-header__title {
  display: flex;
  align-items: center;
  position: relative;
  font-size: 30px;
  font-weight: bold;
  font-style: italic;
  line-height: 0.8;
}
.categories-header__title::before,
.categories-header__title::after {
  content: '';
  position: absolute;
  height: 19px;
  width: 19px;
  transform: rotate(45deg);
  background: #f09d66;
}
.categories-header__title::before {
  left: -110px;
}
.categories-header__title::after {
  right: -110px;
}
.categories-items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 15px;
  grid-template-areas: 'first fifth third' 'second fifth fourth';
  padding: 70px 0;
}
.item-card__first {
  grid-area: first;
}
.item-card__second {
  grid-area: second;
}
.item-card__third {
  grid-area: third;
}
.item-card__fourth {
  grid-area: fourth;
}
.item-card__fifth {
  grid-area: fifth;
}
.categories-item {
  position: relative;
  outline-offset: -20px;
  outline: 3px solid #fff;
  transition: 0.2s;
}
.item-image {
  width: 100%;
  height: 100%;
}
.categories-item:hover,
.item-hover:hover {
  position: absolute;
  top: calc(20px - 3px);
  left: calc(20px - 3px);
  width: calc(100% - (20px - 3px)*2);
  height: calc(100% - (20px - 3px)*2);
  background: rgba(0, 0, 0, 0.4);
}
/*.hover{
  display: none;
}
.item-hover:focus{
  display: block;
}*/
.item-hover__btn {
  position: absolute;
  bottom: 17%;
  left: calc(50% - (110px/2));
  padding: 15px 14px;
  background: #f0975c;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: bold;
  color: #fff;
  z-index: 66;
}
.item-hover__btn:hover {
  background: #fff;
  color: #f0975c;
}
.footer {
  background: #141414;
  padding: 70px 10px;
  line-height: 0.8;
}
.footer-menu {
  display: flex;
  justify-content: space-between;
}
.footer-menu__item {
  display: flex;
  flex-direction: column;
}
.item-header {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  padding-bottom: 50px;
}
.item-list {
  padding: 0;
}
.item-list__point {
  padding-bottom: 20px;
}
.item-list__link {
  font-size: 14px;
  color: #818181;
}
.footer-menu__social {
  padding: 0;
  display: flex;
}
.footer-menu__social-item {
  display: flex;
  align-items: center;
  margin-right: 16px;
}
.footer-menu__link {
  padding: 14px;
  background: #fff;
  border-radius: 50%;
}
.footer-menu__link:hover {
  background-color: #141414;
}
.footer-menu__link .footer-icon:hover {
  fill: #fff;
}
.footer-menu__form {
  display: flex;
  flex-direction: column;
}
.form-title {
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  padding: 50px 0 55px 0;
}
.form-sub {
  display: flex;
}
