// title & pseudo
.categories-header{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: 34px;
}
.categories-header::before,
.categories-header::after{
  content: '';
  position: absolute;
  height: 1px;
  width: 30%;
  background: #e2e2e2;
}
.categories-header::before{
  left: 0;
}
.categories-header::after{
  right: 0;
}
// title
.categories-header__title{
  display: flex;
  align-items: center;
  position: relative;
  font-size: 30px;
  font-weight: bold;
  font-style: italic;
  line-height: .8;
}
.categories-header__title::before,
.categories-header__title::after{
  content: '';
  position: absolute;
  height: 19px;
  width: 19px;
  transform: rotate(45deg);
  background: #f09d66;
}
.categories-header__title::before{
  left: -110px;
}
.categories-header__title::after{
  right: -110px;
}
// grid
.categories-items{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 15px;
  grid-template-areas: 'first fifth third'
                       'second fifth fourth';
  padding: 70px 0;
}
.item-card__first{
  grid-area: first;
}
.item-card__second{
  grid-area: second;
}
.item-card__third{
  grid-area: third;
}
.item-card__fourth{
  grid-area: fourth;
}
.item-card__fifth{
  grid-area: fifth;
}
.categories-item{
  position: relative;
  outline-offset: -20px;
  outline: 3px solid #fff;
  transition: .2s;
}
.item-image{
  width: 100%;
  height: 100%;
}
// hovers
.categories-item:hover,
.item-hover:hover{
  position: absolute;
  top: calc(20px - 3px);
  left: calc(20px - 3px);
  width: calc(100% - (20px - 3px)*2);
  height: calc(100% - (20px - 3px)*2);
  background: rgba(0,0,0,.4);
}
