// buttons
.item-hover__btn{
  position: absolute;
  bottom: 17%;
  left: calc(50% - (110px/2));
  padding: 15px 14px;
  background: #f0975c;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: bold;
  color: #fff;
  z-index: 66;
}
.item-hover__btn:hover{
  background: #fff;
  color: #f0975c;
}